@import "../../styles/_variables";
.input{
    height: 32px;
    max-height:100%;
    min-width:100%;

    &__label{
        font-size: 16px;
        font-weight: 500;
        color: $dark;
    }
    &__field{
        min-width: 100px;
        max-width: 350px;
         width: 100%;
         height: 100%;
         border: 1.8px solid $light-gray;
         border-radius: 4px;
    }
}